<template>
    <div id="app">
        <h2> {{ title }} </h2>
        <p>Click on a character to change it</p>

       <div class="cards">
        <Character  v-for="(id, index) in initial_ids" 
                    v-bind:key="index"
                    :id="id" />
    </div>
    </div>
</template>

<script>
import Character from './components/Character.vue'

export default {
    name: 'app',
    data() {
        return {
            title: 'Choos your team!',
            initial_ids: [1, 13, 14]
        }
    },
    
    components: {
        Character
    }
}
</script>

<style>

</style>





